.portfolio__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 6rem;
    position: fixed;
    top: 0;
    width: 100vw;
    background-color: rgba(255, 255, 255, 0.08);
    backdrop-filter: blur(24px);
    /* box-shadow: 0px 8px 10px rgba(202, 197, 49, 0.5); */
    z-index: 10;    
}

.portfolio__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.portfolio__navbar-links_logo {
    margin-right: 2rem;
}

.portfolio__navbar-links_logo img{
    width: 31px;
    height: 31px;
}
.portfolio__navbar-links_container {
    display: flex;
    flex-direction: row;
}
.portfolio__navbar-links_container p,
.portfolio__navbar-menu_container p {
    color: var(--color-white);
    font-family: var(--font-family-text);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    margin: 0 1rem;
    cursor: pointer;
}
.portfolio__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.portfolio__navbar-menu svg {
    cursor: pointer;
}

.portfolio__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-primary);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.portfolio__navbar-menu_container p {
    margin: 1rem 0;
} 

@media screen and (max-width: 1050px) {

    .portfolio__navbar-links_container {
        display: none;
    }

    .portfolio__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    
    .portfolio__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .portfolio__navbar {
        padding: 2rem;
    }

    .portfolio__navbar-menu_container {
        top: 20px;
    }
}

.flip-in-diag-2-br {
	-webkit-animation: flip-in-diag-2-br 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: flip-in-diag-2-br 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
@-webkit-keyframes flip-in-diag-2-br {
    0% {
      -webkit-transform: rotate3d(-1, 1, 0, -80deg);
              transform: rotate3d(-1, 1, 0, -80deg);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotate3d(1, 1, 0, 0deg);
              transform: rotate3d(1, 1, 0, 0deg);
      opacity: 1;
    }
}
@keyframes flip-in-diag-2-br {
    0% {
      -webkit-transform: rotate3d(-1, 1, 0, -80deg);
              transform: rotate3d(-1, 1, 0, -80deg);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotate3d(1, 1, 0, 0deg);
              transform: rotate3d(1, 1, 0, 0deg);
      opacity: 1;
    }
}
  
  
  